.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    margin: 24px 0 23px;
}

.friendImageAndText {
    display: flex;
    align-items: flex-end;
    max-width: 70%;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;

    border-radius: 50%;
}

.friendText {
    position: relative;
    display: flex;
    flex-direction: column;

    max-width: 100%;

    padding: 7px 25px 8px 13px;

    margin-left: 20px;

    background-color: #f5f7fb;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03),
    0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 10px 10px 10px 0;
}

.friendText:after {
    content: ' ';
    position: absolute;

    width: 0;
    height: 0;

    right: auto;
    left: -15px;
    top: auto;
    bottom: 0;

    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.01),
    0 1px 0 rgba(29, 33, 38, 0.05);

    border-left: 16px solid transparent;
    border-right: 0 solid transparent;
    border-bottom: 16px solid #f5f7fb;
}

.friendName {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    text-align: start;
}

.friendMessageText {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    white-space: pre-wrap;       /* Since CSS 2.1 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    word-wrap: break-word;       /* Internet Explorer 5.5+ */
}

.friendTime {
    width: 48px;
    text-align: center;

    margin-top: 5px;

    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
}
