.sendForm {
    display: flex;
    justify-content: right;
    gap: 44px;
}

.textarea {
    width: 100%;

    padding: 12px 25px;

    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;

    box-sizing: border-box;
    background: #f5f7fb;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03),
    0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    border: none;
    outline: none;
    resize: none;
}

.button {
    width: 120px;
    height: 40px;

    color: white;
    background: #06c;
    border-radius: 20px;
    border: none;

    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

.button:hover {
    cursor: pointer;
}
.button:active {
    cursor: progress;
}
